@charset 'UTF-8';

#wrap {
	min-width: 1100px;
	overflow: hidden;
}

.content_inner {
	width: 930px;
	margin: 30px auto;
}

// 슬라이더 초기화
.swiper-slide {
	display: block;
}

// 공통 타이틀
.common_title {
	color: #1B375B;
	
	font: {
		size: 20px;
		weight: 700;
	}

	&.sm {
		font-size: 17px;
	}
}

// 라인형태 컨텐츠 공통 설정
.line_content_wrap {
	display: block;
	position: relative;
	box-sizing: border-box;
	border-top: 2px solid $navy;

	.item_wrap {
		display: block;
		position: relative;
		overflow: hidden;
		padding: 18px 20px 16px;
		box-sizing: border-box;
	}
}

// 공통 더보기
.baisc_more {
	font-size: 12px;
	font-weight: 600;
	color: #6b798f;

	&:hover,
	&:focus {
		text-decoration: underline;
	}

	> i {
		margin-left: -2px;
		font-weight: bold;
		color: #adb5c2;
		font-size: 11px;
	}
}